import { Visibility, VisibilityOff } from '@mui/icons-material';
import { IconButton, InputAdornment, InputProps, styled } from '@mui/material';
import React, { useMemo, useState } from 'react';
import { TextField as BaseTextField, TextFieldProps } from './textField';

export type PasswordFieldProps = Omit<TextFieldProps, 'type' | 'InputProps'>;

/**
 * TextField with "password reveal" button.
 */
export function PasswordField(props: PasswordFieldProps) {
  const [visible, setVisibility] = useState(false);

  const inputProps: InputProps = useMemo(
    () => ({
      endAdornment: (
        <InputAdornment position="end">
          <IconButton
            onClick={() => setVisibility(!visible)}
            aria-label={visible ? 'Hide password' : 'Show password'}
            edge="end"
            tabIndex={-1}
          >
            {visible ? <VisibilityOff /> : <Visibility />}
          </IconButton>
        </InputAdornment>
      )
    }),
    [visible]
  );

  return (
    <TextField
      {...props}
      type={visible ? 'text' : 'password'}
      valueFormatter={valueFormatter}
      InputProps={inputProps}
      onBlur={event => {
        setVisibility(false);
        props.onBlur?.(event);
      }}
      onKeyDown={event => {
        if (event.key === 'Escape') {
          setVisibility(false);
        }
        props.onKeyDown?.(event);
      }}
    />
  );
}

const TextField = styled(BaseTextField)`
  input::-ms-reveal,
  input::-ms-clear {
    display: none;
  }
`;

function valueFormatter(value: string) {
  return value;
}
